  <p><strong>Note: </strong>Fill text Excerpt from <a href="https://en.wikipedia.org/wiki/HTML" target="_blank">Wikipedia</a></p>
  <h1>WIKI: HTML</h1>
  <article>
    <div class="mw-parser-output">
      <div role="note" class="hatnote navigation-not-searchable">".htm" and ".html" redirect here. For other uses, see <a href="https://en.wikipedia.org/wiki/HTM_(disambiguation)" class="mw-redirect mw-disambig" title="HTM (disambiguation)">HTM</a>.</div>
      <div role="note" class="hatnote navigation-not-searchable"><span class="plainlinks selfreference noprint">For the use of HTML on Wikipedia, see <a href="https://en.wikipedia.org/wiki/Help:HTML_in_wikitext" title="Help:HTML in wikitext">Help:HTML in wikitext</a>.</span></div>
      <p><b>Hypertext Markup Language</b> (<b>HTML</b>) is the standard <a href="https://en.wikipedia.org/wiki/Markup_language" title="Markup language">markup language</a> for creating <a href="https://en.wikipedia.org/wiki/Web_page" title="Web page">web pages</a> and <a href="https://en.wikipedia.org/wiki/Web_application" title="Web application">web applications</a>.
        With <a href="https://en.wikipedia.org/wiki/Cascading_Style_Sheets" title="Cascading Style Sheets">Cascading Style Sheets</a> (CSS) and <a href="https://en.wikipedia.org/wiki/JavaScript" title="JavaScript">JavaScript</a>, it forms a triad of cornerstone technologies for the <a href="https://en.wikipedia.org/wiki/World_Wide_Web"
          title="World Wide Web">World Wide Web</a>.<sup id="cite_ref-4" class="reference"><a href="#cite_note-4">[4]</a></sup></p>
      <p><a href="https://en.wikipedia.org/wiki/Web_browser" title="Web browser">Web browsers</a> receive HTML documents from a <a href="https://en.wikipedia.org/wiki/Web_server" title="Web server">web server</a> or from local storage and <a href="https://en.wikipedia.org/wiki/Web_browser_engine" title="Web browser engine">render</a>        the documents into multimedia web pages. HTML describes the structure of a web page <a href="https://en.wikipedia.org/wiki/Semantic_Web" title="Semantic Web">semantically</a> and originally included cues for the appearance of the document.</p>
      <p><a href="https://en.wikipedia.org/wiki/HTML_element" title="HTML element">HTML elements</a> are the building blocks of HTML pages. With HTML constructs, <a href="https://en.wikipedia.org/wiki/HTML_element#Images_and_objects" title="HTML element">images</a> and other objects such as <a href="https://en.wikipedia.org/wiki/Fieldset"
          class="mw-redirect" title="Fieldset">interactive forms</a> may be embedded into the rendered page. HTML provides a means to create <a href="https://en.wikipedia.org/wiki/Structured_document" title="Structured document">structured documents</a> by denoting structural
        <a href="https://en.wikipedia.org/wiki/Semantics" title="Semantics">semantics</a> for text such as headings, paragraphs, lists, <a href="https://en.wikipedia.org/wiki/Hyperlink" title="Hyperlink">links</a>, quotes and other items. HTML elements are delineated by <i>tags</i>, written using
        <a
          href="https://en.wikipedia.org/wiki/Bracket#Angle_brackets" title="Bracket">angle brackets</a>. Tags such as <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">img</span> <span class="p">/&gt;</span></code> and <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">input</span> <span class="p">/&gt;</span></code>          directly introduce content into the page. Other tags such as <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">p</span><span class="p">&gt;</span></code> surround and provide information about document
          text and may include other tags as sub-elements. Browsers do not display the HTML tags, but use them to interpret the content of the page.</p>
      <p>HTML can embed programs written in a <a href="https://en.wikipedia.org/wiki/Scripting_language" title="Scripting language">scripting language</a> such as <a href="https://en.wikipedia.org/wiki/JavaScript" title="JavaScript">JavaScript</a>, which affects the behavior and content of web pages.
        Inclusion of CSS defines the look and layout of content. The <a href="https://en.wikipedia.org/wiki/World_Wide_Web_Consortium" title="World Wide Web Consortium">World Wide Web Consortium</a> (W3C), maintainer of both the HTML and the CSS standards, has encouraged the
        use of CSS over explicit presentational HTML since 1997.<sup class="plainlinks noexcerpt noprint asof-tag update" style="display:none;"><a class="external text" href="//en.wikipedia.org/w/index.php?title=HTML&amp;action=edit">[update]</a></sup>
        <sup
          id="cite_ref-deprecated_5-0" class="reference"><a href="#cite_note-deprecated-5">[5]</a></sup>
      </p>
      <h2><span class="mw-headline" id="History">History</span></h2>
      <div class="thumb tright">
        <div class="thumbinner" style="width:102px;"><a href="https://en.wikipedia.org/wiki/File:Old_HTML_Logo.svg" class="image"><img alt="Former HTML logo made by the W3C" src="//upload.wikimedia.org/wikipedia/commons/thumb/1/12/Old_HTML_Logo.svg/100px-Old_HTML_Logo.svg.png" width="100" height="100" class="thumbimage" srcset="//upload.wikimedia.org/wikipedia/commons/thumb/1/12/Old_HTML_Logo.svg/150px-Old_HTML_Logo.svg.png 1.5x, //upload.wikimedia.org/wikipedia/commons/thumb/1/12/Old_HTML_Logo.svg/200px-Old_HTML_Logo.svg.png 2x" data-file-width="113" data-file-height="113"></a>
