      <h3><span class="mw-headline" id="Development">Development</span></h3>
      <div class="thumb tright">
        <div class="thumbinner" style="width:172px;"><a href="https://en.wikipedia.org/wiki/File:Tim_Berners-Lee_April_2009.jpg" class="image"><img alt="Photograph of Tim Berners-Lee in April 2009" src="//upload.wikimedia.org/wikipedia/commons/thumb/c/c8/Tim_Berners-Lee_April_2009.jpg/170px-Tim_Berners-Lee_April_2009.jpg" width="170" height="234" class="thumbimage" srcset="//upload.wikimedia.org/wikipedia/commons/thumb/c/c8/Tim_Berners-Lee_April_2009.jpg/255px-Tim_Berners-Lee_April_2009.jpg 1.5x, //upload.wikimedia.org/wikipedia/commons/thumb/c/c8/Tim_Berners-Lee_April_2009.jpg/340px-Tim_Berners-Lee_April_2009.jpg 2x" data-file-width="1195" data-file-height="1648"></a>
          <div class="thumbcaption">
            <div class="magnify"><a href="https://en.wikipedia.org/wiki/File:Tim_Berners-Lee_April_2009.jpg" class="internal" title="Enlarge"></a></div>
            <a href="https://en.wikipedia.org/wiki/Tim_Berners-Lee" title="Tim Berners-Lee">Tim Berners-Lee</a> in April 2009</div>
        </div>
      </div>
      <p>In 1980, physicist <a href="https://en.wikipedia.org/wiki/Tim_Berners-Lee" title="Tim Berners-Lee">Tim Berners-Lee</a>, a contractor at <a href="https://en.wikipedia.org/wiki/CERN" title="CERN">CERN</a>, proposed and prototyped <a href="https://en.wikipedia.org/wiki/ENQUIRE" title="ENQUIRE">ENQUIRE</a>, a system for
        CERN researchers to use and share documents. In 1989, Berners-Lee wrote a memo proposing an <a href="https://en.wikipedia.org/wiki/Internet" title="Internet">Internet</a>-based <a href="https://en.wikipedia.org/wiki/Hypertext" title="Hypertext">hypertext</a> system.<sup id="cite_ref-6" class="reference"><a href="#cite_note-6">[6]</a></sup>        Berners-Lee specified HTML and wrote the browser and server software in late 1990. That year, Berners-Lee and CERN data systems engineer <a href="https://en.wikipedia.org/wiki/Robert_Cailliau" title="Robert Cailliau">Robert Cailliau</a> collaborated on a joint request
        for funding, but the project was not formally adopted by CERN. In his personal notes<sup id="cite_ref-7" class="reference"><a href="#cite_note-7">[7]</a></sup> from 1990 he listed<sup id="cite_ref-8" class="reference"><a href="#cite_note-8">[8]</a></sup>        "some of the many areas in which hypertext is used" and put an encyclopedia first.</p>
      <p>The first publicly available description of HTML was a document called "HTML Tags", first mentioned on the Internet by Tim Berners-Lee in late 1991.<sup id="cite_ref-tagshtml_9-0" class="reference"><a href="#cite_note-tagshtml-9">[9]</a></sup>
        <sup
          id="cite_ref-10" class="reference"><a href="#cite_note-10">[10]</a></sup> It describes 18 elements comprising the initial, relatively simple design of HTML. Except for the hyperlink tag, these were strongly influenced by <a href="https://en.wikipedia.org/wiki/SGMLguid" title="SGMLguid">SGMLguid</a>, an
          in-house <a href="https://en.wikipedia.org/wiki/Standard_Generalized_Markup_Language" title="Standard Generalized Markup Language">Standard Generalized Markup Language</a> (SGML)-based documentation format at CERN. Eleven of these elements still exist in HTML 4.
          <sup
            id="cite_ref-11" class="reference"><a href="#cite_note-11">[11]</a></sup>
      </p>
      <p>HTML is a <a href="https://en.wikipedia.org/wiki/Markup_language" title="Markup language">markup language</a> that <a href="https://en.wikipedia.org/wiki/Web_browser" title="Web browser">web browsers</a> use to interpret and <a href="https://en.wikipedia.org/wiki/Typesetting" title="Typesetting">compose</a> text, images,
        and other material into visual or audible web pages. Default characteristics for every item of HTML markup are defined in the browser, and these characteristics can be altered or enhanced by the web page designer's additional use of <a href="https://en.wikipedia.org/wiki/Cascading_Style_Sheets"
          title="Cascading Style Sheets">CSS</a>. Many of the text elements are found in the 1988 ISO technical report TR 9537 <i>Techniques for using SGML</i>, which in turn covers the features of early text formatting languages such as that used by the
        <a href="https://en.wikipedia.org/wiki/TYPSET_and_RUNOFF" title="TYPSET and RUNOFF">RUNOFF command</a> developed in the early 1960s for the <a href="https://en.wikipedia.org/wiki/Compatible_Time-Sharing_System" title="Compatible Time-Sharing System">CTSS</a> (Compatible Time-Sharing System)
        operating system: these formatting commands were derived from the commands used by typesetters to manually format documents. However, the SGML concept of generalized markup is based on elements (nested annotated ranges with attributes) rather
        than merely print effects, with also the separation of structure and markup; HTML has been progressively moved in this direction with CSS.</p>
      <p>Berners-Lee considered HTML to be an application of SGML. It was formally defined as such by the <a href="https://en.wikipedia.org/wiki/Internet_Engineering_Task_Force" title="Internet Engineering Task Force">Internet Engineering Task Force</a> (IETF) with the mid-1993
        publication of the first proposal for an HTML specification, the "Hypertext Markup Language (HTML)" Internet Draft by Berners-Lee and <a href="https://en.wikipedia.org/wiki/Dan_Connolly_(computer_scientist)" title="Dan Connolly (computer scientist)">Dan Connolly</a>,
        which included an SGML <a href="https://en.wikipedia.org/wiki/Document_type_definition" title="Document type definition">Document type definition</a> to define the grammar.<sup id="cite_ref-12" class="reference"><a href="#cite_note-12">[12]</a></sup><sup id="cite_ref-13"
          class="reference"><a href="#cite_note-13">[13]</a></sup> The draft expired after six months, but was notable for its acknowledgment of the <a href="https://en.wikipedia.org/wiki/Mosaic_(web_browser)" title="Mosaic (web browser)">NCSA Mosaic</a> browser's custom tag for
        embedding in-line images, reflecting the IETF's philosophy of basing standards on successful prototypes.<sup id="cite_ref-raymond_14-0" class="reference"><a href="#cite_note-raymond-14">[14]</a></sup> Similarly, <a href="https://en.wikipedia.org/wiki/Dave_Raggett" title="Dave Raggett">Dave Raggett</a>'s
        competing Internet-Draft, "HTML+ (Hypertext Markup Format)", from late 1993, suggested standardizing already-implemented features like tables and fill-out forms.<sup id="cite_ref-html+_15-0" class="reference"><a href="#cite_note-html+-15">[15]</a></sup></p>
      <p>After the HTML and HTML+ drafts expired in early 1994, the IETF created an HTML Working Group, which in 1995 completed "HTML 2.0", the first HTML specification intended to be treated as a standard against which future implementations should be based.
        <sup
          id="cite_ref-16" class="reference"><a href="#cite_note-16">[16]</a></sup>
      </p>
      <p>Further development under the auspices of the IETF was stalled by competing interests. Since 1996,<sup class="plainlinks noexcerpt noprint asof-tag update" style="display:none;"><a class="external text" href="//en.wikipedia.org/w/index.php?title=HTML&amp;action=edit">[update]</a></sup>        the HTML specifications have been maintained, with input from commercial software vendors, by the <a href="https://en.wikipedia.org/wiki/World_Wide_Web_Consortium" title="World Wide Web Consortium">World Wide Web Consortium</a> (W3C).<sup id="cite_ref-raggett_17-0" class="reference"><a href="#cite_note-raggett-17">[17]</a></sup>        However, in 2000, HTML also became an international standard (<a href="https://en.wikipedia.org/wiki/International_Organization_for_Standardization" title="International Organization for Standardization">ISO</a>/<a href="https://en.wikipedia.org/wiki/International_Electrotechnical_Commission"
          title="International Electrotechnical Commission">IEC</a> 15445:2000). HTML 4.01 was published in late 1999, with further errata published through 2001. In 2004, development began on HTML5 in the <a href="https://en.wikipedia.org/wiki/Web_Hypertext_Application_Technology_Working_Group"
          class="mw-redirect" title="Web Hypertext Application Technology Working Group">Web Hypertext Application Technology Working Group</a> (WHATWG), which became a joint deliverable with the W3C in 2008, and completed and standardized on 28 October
        2014.<sup id="cite_ref-18" class="reference"><a href="#cite_note-18">[18]</a></sup></p>
      <h3><span class="mw-headline" id="HTML_versions_timeline">HTML versions timeline</span></h3>
      <dl>
        <dt>November 24, 1995</dt>
        <dd>HTML 2.0 was published as <a href="https://en.wikipedia.org/wiki/Request_for_Comments_(identifier)" class="mw-redirect" title="Request for Comments (identifier)">RFC</a>&nbsp;<a rel="nofollow" class="external text" href="https://tools.ietf.org/html/rfc1866">1866</a>.
          Supplemental <a href="https://en.wikipedia.org/wiki/Request_for_Comments" title="Request for Comments">RFCs</a> added capabilities:
          <ul>
            <li>November 25, 1995: <a href="https://en.wikipedia.org/wiki/Request_for_Comments_(identifier)" class="mw-redirect" title="Request for Comments (identifier)">RFC</a>&nbsp;<a rel="nofollow" class="external text" href="https://tools.ietf.org/html/rfc1867">1867</a> (form-based
              file upload)</li>
            <li>May 1996: <a href="https://en.wikipedia.org/wiki/Request_for_Comments_(identifier)" class="mw-redirect" title="Request for Comments (identifier)">RFC</a>&nbsp;<a rel="nofollow" class="external text" href="https://tools.ietf.org/html/rfc1942">1942</a> (tables)</li>
            <li>August 1996: <a href="https://en.wikipedia.org/wiki/Request_for_Comments_(identifier)" class="mw-redirect" title="Request for Comments (identifier)">RFC</a>&nbsp;<a rel="nofollow" class="external text" href="https://tools.ietf.org/html/rfc1980">1980</a> (client-side
              image maps)</li>
            <li>January 1997: <a href="https://en.wikipedia.org/wiki/Request_for_Comments_(identifier)" class="mw-redirect" title="Request for Comments (identifier)">RFC</a>&nbsp;<a rel="nofollow" class="external text" href="https://tools.ietf.org/html/rfc2070">2070</a> (<a href="https://en.wikipedia.org/wiki/Internationalization_and_localization"
                title="Internationalization and localization">internationalization</a>)</li>
          </ul>
        </dd>
      </dl>
      <dl>
        <dt>January 14, 1997</dt>
        <dd>HTML 3.2<sup id="cite_ref-19" class="reference"><a href="#cite_note-19">[19]</a></sup> was published as a <a href="https://en.wikipedia.org/wiki/W3C_Recommendation" class="mw-redirect" title="W3C Recommendation">W3C Recommendation</a>. It was the first version developed
          and standardized exclusively by the W3C, as the IETF had closed its HTML Working Group on September 12, 1996.<sup id="cite_ref-20" class="reference"><a href="#cite_note-20">[20]</a></sup></dd>
        <dd>Initially code-named "Wilbur",<sup id="cite_ref-engelfriet_21-0" class="reference"><a href="#cite_note-engelfriet-21">[21]</a></sup> HTML 3.2 dropped math formulas entirely, reconciled overlap among various proprietary extensions and adopted most
          of <a href="https://en.wikipedia.org/wiki/Netscape" title="Netscape">Netscape</a>'s visual markup tags. Netscape's <a href="https://en.wikipedia.org/wiki/Blink_element" title="Blink element">blink element</a> and <a href="https://en.wikipedia.org/wiki/Microsoft" title="Microsoft">Microsoft</a>'s <a href="https://en.wikipedia.org/wiki/Marquee_element"
            title="Marquee element">marquee element</a> were omitted due to a mutual agreement between the two companies.<sup id="cite_ref-raggett_17-1" class="reference"><a href="#cite_note-raggett-17">[17]</a></sup> A markup for mathematical formulas
          similar to that in HTML was not standardized until 14 months later in <a href="https://en.wikipedia.org/wiki/MathML" title="MathML">MathML</a>.</dd>
        <dt>December 18, 1997</dt>
        <dd>HTML 4.0<sup id="cite_ref-22" class="reference"><a href="#cite_note-22">[22]</a></sup> was published as a W3C Recommendation. It offers three variations:
          <ul>
            <li>Strict, in which deprecated elements are forbidden</li>
            <li>Transitional, in which deprecated elements are allowed</li>
            <li>Frameset, in which mostly only <a href="https://en.wikipedia.org/wiki/Framing_(World_Wide_Web)" title="Framing (World Wide Web)">frame</a> related elements are allowed.</li>
          </ul>
        </dd>
        <dd>Initially code-named "Cougar",<sup id="cite_ref-engelfriet_21-1" class="reference"><a href="#cite_note-engelfriet-21">[21]</a></sup> HTML 4.0 adopted many browser-specific element types and attributes, but at the same time sought to phase out
          Netscape's visual markup features by marking them as <a href="https://en.wikipedia.org/wiki/Deprecation" title="Deprecation">deprecated</a> in favor of style sheets. HTML 4 is an SGML application conforming to ISO 8879&nbsp;– SGML.<sup id="cite_ref-23" class="reference"><a href="#cite_note-23">[23]</a></sup></dd>
        <dt>April 24, 1998</dt>
        <dd>HTML 4.0<sup id="cite_ref-24" class="reference"><a href="#cite_note-24">[24]</a></sup> was reissued with minor edits without incrementing the version number.</dd>
        <dt>December 24, 1999</dt>
        <dd>HTML 4.01<sup id="cite_ref-25" class="reference"><a href="#cite_note-25">[25]</a></sup> was published as a W3C Recommendation. It offers the same three variations as HTML 4.0 and its last <a rel="nofollow" class="external text" href="https://www.w3.org/MarkUp/html4-updates/errata">errata</a>          were published on May 12, 2001.</dd>
        <dt>May 2000</dt>
        <dd>ISO/IEC 15445:2000<sup id="cite_ref-iso-html_26-0" class="reference"><a href="#cite_note-iso-html-26">[26]</a></sup><sup id="cite_ref-27" class="reference"><a href="#cite_note-27">[27]</a></sup> ("<a href="https://en.wikipedia.org/wiki/International_Organization_for_Standardization"
            title="International Organization for Standardization">ISO</a> HTML", based on HTML 4.01 Strict) was published as an ISO/IEC international standard. In the ISO this standard falls in the domain of the <a href="https://en.wikipedia.org/wiki/ISO/IEC_JTC1/SC34" class="mw-redirect"
            title="ISO/IEC JTC1/SC34">ISO/IEC JTC1/SC34</a> (ISO/IEC Joint Technical Committee 1, Subcommittee 34&nbsp;– Document description and processing languages).<sup id="cite_ref-iso-html_26-1" class="reference"><a href="#cite_note-iso-html-26">[26]</a></sup></dd>
        <dd>After HTML 4.01, there was no new version of HTML for many years as development of the parallel, XML-based language XHTML occupied the W3C's HTML Working Group through the early and mid-2000s.</dd>
        <dt>October 28, 2014</dt>
        <dd>HTML5<sup id="cite_ref-28" class="reference"><a href="#cite_note-28">[28]</a></sup> was published as a W3C Recommendation.<sup id="cite_ref-29" class="reference"><a href="#cite_note-29">[29]</a></sup></dd>
        <dt>November 1, 2016</dt>
        <dd>HTML 5.1<sup id="cite_ref-30" class="reference"><a href="#cite_note-30">[30]</a></sup> was published as a W3C Recommendation.<sup id="cite_ref-31" class="reference"><a href="#cite_note-31">[31]</a></sup><sup id="cite_ref-32" class="reference"><a href="#cite_note-32">[32]</a></sup></dd>
        <dt>December 14, 2017</dt>
        <dd>HTML 5.2<sup id="cite_ref-33" class="reference"><a href="#cite_note-33">[33]</a></sup> was published as a W3C Recommendation.<sup id="cite_ref-34" class="reference"><a href="#cite_note-34">[34]</a></sup><sup id="cite_ref-35" class="reference"><a href="#cite_note-35">[35]</a></sup></dd>
      </dl>
      <h4><span class="mw-headline" id="HTML_draft_version_timeline">HTML draft version timeline</span></h4>
      <div class="thumb tright">
        <div class="thumbinner" style="width:202px;"><a href="https://en.wikipedia.org/wiki/File:HTML5_logo_and_wordmark.svg" class="image"><img alt="Official HTML5 logo" src="//upload.wikimedia.org/wikipedia/commons/thumb/6/61/HTML5_logo_and_wordmark.svg/200px-HTML5_logo_and_wordmark.svg.png" width="200" height="200" class="thumbimage" srcset="//upload.wikimedia.org/wikipedia/commons/thumb/6/61/HTML5_logo_and_wordmark.svg/300px-HTML5_logo_and_wordmark.svg.png 1.5x, //upload.wikimedia.org/wikipedia/commons/thumb/6/61/HTML5_logo_and_wordmark.svg/400px-HTML5_logo_and_wordmark.svg.png 2x" data-file-width="512" data-file-height="512"></a>
          <div class="thumbcaption">
            <div class="magnify"><a href="https://en.wikipedia.org/wiki/File:HTML5_logo_and_wordmark.svg" class="internal" title="Enlarge"></a></div>
            Logo of HTML5</div>
        </div>
      </div>
      <dl>
        <dt>October 1991</dt>
        <dd><i>HTML Tags</i>,<sup id="cite_ref-tagshtml_9-1" class="reference"><a href="#cite_note-tagshtml-9">[9]</a></sup> an informal CERN document listing 18 HTML tags, was first mentioned in public.</dd>
        <dt>June 1992</dt>
        <dd>First informal draft of the HTML DTD,<sup id="cite_ref-36" class="reference"><a href="#cite_note-36">[36]</a></sup> with seven<sup id="cite_ref-37" class="reference"><a href="#cite_note-37">[37]</a></sup><sup id="cite_ref-38" class="reference"><a href="#cite_note-38">[38]</a></sup>
          <sup
            id="cite_ref-html11_39-0" class="reference"><a href="#cite_note-html11-39">[39]</a></sup> subsequent revisions (July 15, August 6, August 18, November 17, November 19, November 20, November 22)</dd>
        <dt>November 1992</dt>
        <dd>HTML DTD 1.1 (the first with a version number, based on RCS revisions, which start with 1.1 rather than 1.0), an informal draft<sup id="cite_ref-html11_39-1" class="reference"><a href="#cite_note-html11-39">[39]</a></sup></dd>
        <dt>June 1993</dt>
        <dd>Hypertext Markup Language<sup id="cite_ref-40" class="reference"><a href="#cite_note-40">[40]</a></sup> was published by the <a href="https://en.wikipedia.org/wiki/Internet_Engineering_Task_Force" title="Internet Engineering Task Force">IETF</a> IIIR Working Group as
          an Internet Draft (a rough proposal for a standard). It was replaced by a second version<sup id="cite_ref-ietfiiir_41-0" class="reference"><a href="#cite_note-ietfiiir-41">[41]</a></sup> one month later, followed by six further drafts published
          by IETF itself<sup id="cite_ref-42" class="reference"><a href="#cite_note-42">[42]</a></sup> that finally led to HTML 2.0 in <a href="https://en.wikipedia.org/wiki/Request_for_Comments_(identifier)" class="mw-redirect" title="Request for Comments (identifier)">RFC</a>&nbsp;
          <a
            rel="nofollow" class="external text" href="https://tools.ietf.org/html/rfc1866">1866</a>.</dd>
        <dt>November 1993</dt>
        <dd>HTML+ was published by the IETF as an Internet Draft and was a competing proposal to the Hypertext Markup Language draft. It expired in May 1994.</dd>
        <dt>April 1995 (authored March 1995)</dt>
        <dd>HTML 3.0<sup id="cite_ref-43" class="reference"><a href="#cite_note-43">[43]</a></sup> was proposed as a standard to the IETF, but the proposal expired five months later (28 September 1995)<sup id="cite_ref-html30cover_44-0" class="reference"><a href="#cite_note-html30cover-44">[44]</a></sup>          without further action. It included many of the capabilities that were in Raggett's HTML+ proposal, such as support for tables, text flow around figures and the display of complex mathematical formulas.<sup id="cite_ref-html30cover_44-1" class="reference"><a href="#cite_note-html30cover-44">[44]</a></sup></dd>
        <dd>W3C began development of its own <a href="https://en.wikipedia.org/wiki/Arena_(web_browser)" title="Arena (web browser)">Arena browser</a> as a <a href="https://en.wikipedia.org/wiki/Test_bed" class="mw-redirect" title="Test bed">test bed</a> for HTML 3 and Cascading Style Sheets,<sup id="cite_ref-45"
            class="reference"><a href="#cite_note-45">[45]</a></sup><sup id="cite_ref-46" class="reference"><a href="#cite_note-46">[46]</a></sup><sup id="cite_ref-47" class="reference"><a href="#cite_note-47">[47]</a></sup> but HTML 3.0 did not succeed
          for several reasons. The draft was considered very large at 150 pages and the pace of browser development, as well as the number of interested parties, had outstripped the resources of the IETF.<sup id="cite_ref-raggett_17-2" class="reference"><a href="#cite_note-raggett-17">[17]</a></sup>          Browser vendors, including Microsoft and Netscape at the time, chose to implement different subsets of HTML 3's draft features as well as to introduce their own extensions to it.<sup id="cite_ref-raggett_17-3" class="reference"><a href="#cite_note-raggett-17">[17]</a></sup>          (see <a href="https://en.wikipedia.org/wiki/Browser_wars" title="Browser wars">Browser wars</a>). These included extensions to control stylistic aspects of documents, contrary to the "belief [of the academic engineering community] that such things as text color, background
          texture, font size and font face were definitely outside the scope of a language when their only intent was to specify how a document would be organized."<sup id="cite_ref-raggett_17-4" class="reference"><a href="#cite_note-raggett-17">[17]</a></sup>          Dave Raggett, who has been a W3C Fellow for many years, has commented for example: "To a certain extent, Microsoft built its business on the Web by extending HTML features."<sup id="cite_ref-raggett_17-5" class="reference"><a href="#cite_note-raggett-17">[17]</a></sup></dd>
        <dt>January 2008</dt>
        <dd><a href="https://en.wikipedia.org/wiki/HTML5" title="HTML5">HTML5</a> was published as a <a href="https://en.wikipedia.org/wiki/World_Wide_Web_Consortium#Recommendations_and_Certifications" title="World Wide Web Consortium">Working Draft</a> by the W3C.<sup id="cite_ref-48" class="reference"><a href="#cite_note-48">[48]</a></sup></dd>
        <dd>Although its syntax closely resembles that of <a href="https://en.wikipedia.org/wiki/SGML" class="mw-redirect" title="SGML">SGML</a>, <a href="https://en.wikipedia.org/wiki/HTML5" title="HTML5">HTML5</a> has abandoned any attempt to be an SGML application and has explicitly defined its own
          "html" serialization, in addition to an alternative XML-based XHTML5 serialization.<sup id="cite_ref-49" class="reference"><a href="#cite_note-49">[49]</a></sup></dd>
        <dt>2011&nbsp;HTML5 – Last Call</dt>
        <dd>On 14 February 2011, the W3C extended the charter of its HTML Working Group with clear milestones for HTML5. In May 2011, the working group advanced HTML5 to "Last Call", an invitation to communities inside and outside W3C to confirm the technical
          soundness of the specification. The W3C developed a comprehensive test suite to achieve broad interoperability for the full specification by 2014, which was the target date for recommendation.<sup id="cite_ref-w3c2014_50-0" class="reference"><a href="#cite_note-w3c2014-50">[50]</a></sup>          In January 2011, the WHATWG renamed its "HTML5" living standard to "HTML". The W3C nevertheless continues its project to release HTML5.<sup id="cite_ref-51" class="reference"><a href="#cite_note-51">[51]</a></sup></dd>
        <dt>2012&nbsp;HTML5 – Candidate Recommendation</dt>
        <dd>In July 2012, WHATWG and <a href="https://en.wikipedia.org/wiki/W3C" class="mw-redirect" title="W3C">W3C</a> decided on a degree of separation. W3C will continue the HTML5 specification work, focusing on a single definitive standard, which is considered as a "snapshot"
          by WHATWG. The WHATWG organization will continue its work with HTML5 as a "Living Standard". The concept of a living standard is that it is never complete and is always being updated and improved. New features can be added but functionality
          will not be removed.<sup id="cite_ref-52" class="reference"><a href="#cite_note-52">[52]</a></sup></dd>
        <dd>In December 2012, W3C designated HTML5 as a Candidate Recommendation.<sup id="cite_ref-53" class="reference"><a href="#cite_note-53">[53]</a></sup> The criterion for advancement to <a href="https://en.wikipedia.org/wiki/W3C_recommendation#W3C_Recommendation_(REC)" class="mw-redirect"
            title="W3C recommendation">W3C Recommendation</a> is "two 100% complete and fully interoperable implementations".<sup id="cite_ref-W3Crec_54-0" class="reference"><a href="#cite_note-W3Crec-54">[54]</a></sup></dd>
        <dt>2014&nbsp;HTML5 – Proposed Recommendation and Recommendation</dt>
        <dd>In September 2014, W3C moved HTML5 to Proposed Recommendation.<sup id="cite_ref-55" class="reference"><a href="#cite_note-55">[55]</a></sup></dd>
        <dd>On 28 October 2014, HTML5 was released as a stable W3C Recommendation,<sup id="cite_ref-56" class="reference"><a href="#cite_note-56">[56]</a></sup> meaning the specification process is complete.<sup id="cite_ref-finalars_57-0" class="reference"><a href="#cite_note-finalars-57">[57]</a></sup></dd>
      </dl>
      <h4><span class="mw-headline" id="XHTML_versions">XHTML versions</span></h4>
      <div role="note" class="hatnote navigation-not-searchable">Main article: <a href="https://en.wikipedia.org/wiki/XHTML" title="XHTML">XHTML</a></div>
      <p>XHTML is a separate language that began as a reformulation of HTML 4.01 using <a href="https://en.wikipedia.org/wiki/XML" title="XML">XML</a> 1.0. It is no longer being developed as a separate standard.</p>
      <ul>
        <li>XHTML 1.0 was published as a W3C Recommendation on January 26, 2000,<sup id="cite_ref-58" class="reference"><a href="#cite_note-58">[58]</a></sup> and was later revised and republished on August 1, 2002. It offers the same three variations as
          HTML 4.0 and 4.01, reformulated in XML, with minor restrictions.</li>
        <li>XHTML 1.1<sup id="cite_ref-59" class="reference"><a href="#cite_note-59">[59]</a></sup> was published as a W3C Recommendation on May 31, 2001. It is based on XHTML 1.0 Strict, but includes minor changes, can be customized, and is reformulated
          using modules in the W3C recommendation "Modularization of XHTML", which was published on April 10, 2001.<sup id="cite_ref-60" class="reference"><a href="#cite_note-60">[60]</a></sup></li>
        <li>XHTML 2.0 was a working draft, work on it was abandoned in 2009 in favor of work on <a href="https://en.wikipedia.org/wiki/HTML5" title="HTML5">HTML5</a> and <a href="https://en.wikipedia.org/wiki/XHTML#XHTML5" title="XHTML">XHTML5</a>.<sup id="cite_ref-61" class="reference"><a href="#cite_note-61">[61]</a></sup>
          <sup
            id="cite_ref-62" class="reference"><a href="#cite_note-62">[62]</a></sup><sup id="cite_ref-63" class="reference"><a href="#cite_note-63">[63]</a></sup> XHTML 2.0 was incompatible with XHTML 1.x and, therefore, would be more accurately characterized as an XHTML-inspired new language
            than an update to XHTML 1.x.</li>
        <li>An XHTML syntax, known as "XHTML5.1", is being defined alongside <a href="https://en.wikipedia.org/wiki/HTML5" title="HTML5">HTML5</a> in the HTML5 draft.<sup id="cite_ref-64" class="reference"><a href="#cite_note-64">[64]</a></sup></li>
      </ul>
      <h2><span class="mw-headline" id="Markup">Markup</span></h2>
      <p>HTML markup consists of several key components, including those called <i>tags</i> (and their <i>attributes</i>), character-based <i>data types</i>, <i>character references</i> and <i>entity references</i>. HTML tags most commonly come in pairs
        like <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">h1</span><span class="p">&gt;</span></code> and <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;/</span><span class="nt">h1</span><span class="p">&gt;</span></code>,
        although some represent <i>empty elements</i> and so are unpaired, for example <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">img</span><span class="p">&gt;</span></code>. The first tag in such
        a pair is the <i>start tag</i>, and the second is the <i>end tag</i> (they are also called <i>opening tags</i> and <i>closing tags</i>).</p>
      <p>Another important component is the HTML <i><a href="https://en.wikipedia.org/wiki/Document_type_declaration" title="Document type declaration">document type declaration</a></i>, which triggers <a href="https://en.wikipedia.org/wiki/Standards_mode" class="mw-redirect" title="Standards mode">standards mode</a>        rendering.</p>
      <p>The following is an example of the classic <a href="https://en.wikipedia.org/wiki/%22Hello,_World!%22_program" title="&quot;Hello, World!&quot; program">"Hello, World!" program</a>, a common test employed for comparing <a href="https://en.wikipedia.org/wiki/Programming_language" title="Programming language">programming languages</a>,
        <a href="https://en.wikipedia.org/wiki/Scripting_language" title="Scripting language">scripting languages</a> and <a href="https://en.wikipedia.org/wiki/Markup_language" title="Markup language">markup languages</a>. This example is made using 9 <a href="https://en.wikipedia.org/wiki/Source_lines_of_code" title="Source lines of code">source lines of code</a>:</p>
      <div class="mw-highlight mw-content-ltr" dir="ltr">
        <pre><span class="cp">&lt;!DOCTYPE html&gt;</span>
<span class="p">&lt;</span><span class="nt">html</span><span class="p">&gt;</span>
  <span class="p">&lt;</span><span class="nt">head</span><span class="p">&gt;</span>
    <span class="p">&lt;</span><span class="nt">title</span><span class="p">&gt;</span>This is a title<span class="p">&lt;/</span><span class="nt">title</span><span class="p">&gt;</span>
  <span class="p">&lt;/</span><span class="nt">head</span><span class="p">&gt;</span>
  <span class="p">&lt;</span><span class="nt">body</span><span class="p">&gt;</span>
    <span class="p">&lt;</span><span class="nt">p</span><span class="p">&gt;</span>Hello world!<span class="p">&lt;/</span><span class="nt">p</span><span class="p">&gt;</span>
  <span class="p">&lt;/</span><span class="nt">body</span><span class="p">&gt;</span>
<span class="p">&lt;/</span><span class="nt">html</span><span class="p">&gt;</span>
</pre></div>
      <p>The text between <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">html</span><span class="p">&gt;</span></code> and <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;/</span><span class="nt">html</span><span class="p">&gt;</span></code>        describes the web page, and the text between <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">body</span><span class="p">&gt;</span></code> and <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;/</span><span class="nt">body</span><span class="p">&gt;</span></code>        is the visible page content. The markup text <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">title</span><span class="p">&gt;</span>This is a title<span class="p">&lt;/</span><span class="nt">title</span><span class="p">&gt;</span></code>        defines the browser page title.</p>
      <p>The Document Type Declaration <code class="mw-highlight" id="" style="" dir="ltr"><span class="cp">&lt;!DOCTYPE html&gt;</span></code> is for HTML5. If a declaration is not included, various browsers will revert to "<a href="https://en.wikipedia.org/wiki/Quirks_mode" title="Quirks mode">quirks mode</a>"
        for rendering.<sup id="cite_ref-hsivonen_65-0" class="reference"><a href="#cite_note-hsivonen-65">[65]</a></sup></p>
      <h3><span class="mw-headline" id="Elements">Elements</span></h3>
      <div role="note" class="hatnote navigation-not-searchable">Main article: <a href="https://en.wikipedia.org/wiki/HTML_element" title="HTML element">HTML element</a></div>
      <p>HTML documents imply a structure of nested <a href="https://en.wikipedia.org/wiki/HTML_element" title="HTML element">HTML elements</a>. These are indicated in the document by HTML <i>tags</i>, enclosed in angle brackets thus: <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">p</span><span class="p">&gt;</span></code>.
        <sup
          id="cite_ref-66" class="reference"><a href="#cite_note-66">[66]</a></sup>
      </p>
      <p>In the simple, general case, the extent of an element is indicated by a pair of tags: a "start tag" <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">p</span><span class="p">&gt;</span></code> and "end
        tag" <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;/</span><span class="nt">p</span><span class="p">&gt;</span></code>. The text content of the element, if any, is placed between these tags.</p>
      <p>Tags may also enclose further tag markup between the start and end, including a mixture of tags and text. This indicates further (nested) elements, as children of the parent element.</p>
      <p>The start tag may also include <i>attributes</i> within the tag. These indicate other information, such as identifiers for sections within the document, identifiers used to bind style information to the presentation of the document, and for some
        tags such as the <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">img</span><span class="p">&gt;</span></code> used to embed images, the reference to the image resource.</p>
      <p>Some elements, such as the <a href="https://en.wikipedia.org/wiki/Line_breaking_character" class="mw-redirect" title="Line breaking character">line break</a> <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">br</span><span class="p">&gt;</span></code>,
        do not permit <i>any</i> embedded content, either text or further tags. These require only a single empty tag (akin to a start tag) and do not use an end tag.</p>
      <p>Many tags, particularly the closing end tag for the very commonly used paragraph element <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">p</span><span class="p">&gt;</span></code>, are optional. An
        HTML browser or other agent can infer the closure for the end of an element from the context and the structural rules defined by the HTML standard. These rules are complex and not widely understood by most HTML coders.</p>
      <p>The general form of an HTML element is therefore: <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">tag</span> <span class="na">attribute1</span><span class="o">=</span><span class="s">"value1"</span> <span class="na">attribute2</span><span class="o">=</span><span class="s">"value2"</span><span class="p">&gt;</span>''content''<span class="p">&lt;/</span><span class="nt">tag</span><span class="p">&gt;</span></code>.
        Some HTML elements are defined as <i>empty elements</i> and take the form <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">tag</span> <span class="na">attribute1</span><span class="o">=</span><span class="s">"value1"</span> <span class="na">attribute2</span><span class="o">=</span><span class="s">"value2"</span><span class="p">&gt;</span></code>.
        Empty elements may enclose no content, for instance, the <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">br</span><span class="p">&gt;</span></code> tag or the inline <code class="mw-highlight" id=""
          style="" dir="ltr"><span class="p">&lt;</span><span class="nt">img</span><span class="p">&gt;</span></code> tag. The name of an HTML element is the name used in the tags. Note that the end tag's name is preceded by a slash character, <code>/</code>,
        and that in empty elements the end tag is neither required nor allowed. If attributes are not mentioned, default values are used in each case.</p>
      <h4><span class="mw-headline" id="Element_examples">Element examples</span></h4>
      <p>Header of the HTML document: <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">head</span><span class="p">&gt;</span>...<span class="p">&lt;/</span><span class="nt">head</span><span class="p">&gt;</span></code>.
        The title is included in the head, for example:</p>
      <div class="mw-highlight mw-content-ltr" dir="ltr">
        <pre><span class="p">&lt;</span><span class="nt">head</span><span class="p">&gt;</span>
  <span class="p">&lt;</span><span class="nt">title</span><span class="p">&gt;</span>The Title<span class="p">&lt;/</span><span class="nt">title</span><span class="p">&gt;</span>
<span class="p">&lt;/</span><span class="nt">head</span><span class="p">&gt;</span>
</pre></div>
      <p>Headings: HTML headings are defined with the <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">h1</span><span class="p">&gt;</span></code> to <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">h6</span><span class="p">&gt;</span></code>        tags:</p>
      <div class="mw-highlight mw-content-ltr" dir="ltr">
        <pre><span class="p">&lt;</span><span class="nt">h1</span><span class="p">&gt;</span>Heading level 1<span class="p">&lt;/</span><span class="nt">h1</span><span class="p">&gt;</span>
<span class="p">&lt;</span><span class="nt">h2</span><span class="p">&gt;</span>Heading level 2<span class="p">&lt;/</span><span class="nt">h2</span><span class="p">&gt;</span>
<span class="p">&lt;</span><span class="nt">h3</span><span class="p">&gt;</span>Heading level 3<span class="p">&lt;/</span><span class="nt">h3</span><span class="p">&gt;</span>
<span class="p">&lt;</span><span class="nt">h4</span><span class="p">&gt;</span>Heading level 4<span class="p">&lt;/</span><span class="nt">h4</span><span class="p">&gt;</span>
<span class="p">&lt;</span><span class="nt">h5</span><span class="p">&gt;</span>Heading level 5<span class="p">&lt;/</span><span class="nt">h5</span><span class="p">&gt;</span>
<span class="p">&lt;</span><span class="nt">h6</span><span class="p">&gt;</span>Heading level 6<span class="p">&lt;/</span><span class="nt">h6</span><span class="p">&gt;</span>
</pre></div>
      <p>Paragraphs:</p>
      <div class="mw-highlight mw-content-ltr" dir="ltr">
        <pre><span class="p">&lt;</span><span class="nt">p</span><span class="p">&gt;</span>Paragraph 1<span class="p">&lt;/</span><span class="nt">p</span><span class="p">&gt;</span> <span class="p">&lt;</span><span class="nt">p</span><span class="p">&gt;</span>Paragraph 2<span class="p">&lt;/</span><span class="nt">p</span><span class="p">&gt;</span>
</pre></div>
      <p>Line breaks: <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">br</span><span class="p">&gt;</span></code>. The difference between <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">br</span><span class="p">&gt;</span></code>        and <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">p</span><span class="p">&gt;</span></code> is that <code>br</code> <a href="https://en.wikipedia.org/wiki/Line_breaking_character" class="mw-redirect" title="Line breaking character">breaks a line</a>        without altering the semantic structure of the page, whereas <code>p</code> sections the page into <a href="https://en.wikipedia.org/wiki/Paragraph" title="Paragraph">paragraphs</a>. Note also that <code>br</code> is an <i>empty element</i> in that, although it may have
        attributes, it can take no content and it may not have an end tag.</p>
      <div class="mw-highlight mw-content-ltr" dir="ltr">
        <pre><span class="p">&lt;</span><span class="nt">p</span><span class="p">&gt;</span>This <span class="p">&lt;</span><span class="nt">br</span><span class="p">&gt;</span> is a paragraph <span class="p">&lt;</span><span class="nt">br</span><span class="p">&gt;</span> with <span class="p">&lt;</span><span class="nt">br</span><span class="p">&gt;</span> line breaks<span class="p">&lt;/</span><span class="nt">p</span><span class="p">&gt;</span>
</pre></div>
      <p>This is a link in HTML. To create a link the <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">a</span><span class="p">&gt;</span></code> tag is used. The <code>href</code> attribute holds the URL address
        of the link.</p>
      <div class="mw-highlight mw-content-ltr" dir="ltr">
        <pre><span class="p">&lt;</span><span class="nt">a</span> <span class="na">href</span><span class="o">=</span><span class="s">"https://www.wikipedia.org/"</span><span class="p">&gt;</span>A link to Wikipedia!<span class="p">&lt;/</span><span class="nt">a</span><span class="p">&gt;</span>
</pre></div>
      <p>Inputs:</p>
      <p>There are many possible ways a user can give input/s like:</p>
      <div class="mw-highlight mw-content-ltr" dir="ltr">
        <pre><span class="lineno">1 </span><span class="p">&lt;</span><span class="nt">input</span> <span class="na">type</span><span class="o">=</span><span class="s">"text"</span> <span class="p">/&gt;</span> <span class="c">&lt;!-- This is for text input --&gt;</span>
<span class="lineno">2 </span><span class="p">&lt;</span><span class="nt">input</span> <span class="na">type</span><span class="o">=</span><span class="s">"file"</span> <span class="p">/&gt;</span> <span class="c">&lt;!-- This is for uploading files --&gt;</span>
<span class="lineno">3 </span><span class="p">&lt;</span><span class="nt">input</span> <span class="na">type</span><span class="o">=</span><span class="s">"checkbox"</span> <span class="p">/&gt;</span> <span class="c">&lt;!-- This is for checkboxes --&gt;</span>
</pre></div>
      <p><span id="Comments"></span><span id="comments"></span>Comments:</p>
      <div class="mw-highlight mw-content-ltr" dir="ltr">
        <pre><span class="c">&lt;!-- This is a comment --&gt;</span>
</pre></div>
      <p>Comments can help in the understanding of the markup and do not display in the webpage.</p>
      <p>There are several types of markup elements used in HTML:</p>
      <dl>
        <dt>Structural markup indicates the purpose of text</dt>
        <dd>For example, <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">h2</span><span class="p">&gt;</span>Golf<span class="p">&lt;/</span><span class="nt">h2</span><span class="p">&gt;</span></code> establishes
          "Golf" as a second-level <a href="https://en.wikipedia.org/wiki/HTML_element#Basic_text" title="HTML element">heading</a>. Structural markup does not denote any specific rendering, but most web browsers have default styles for element formatting. Content may be further
          styled using <a href="https://en.wikipedia.org/wiki/Cascading_Style_Sheets" title="Cascading Style Sheets">Cascading Style Sheets</a> (CSS).<sup id="cite_ref-67" class="reference"><a href="#cite_note-67">[67]</a></sup></dd>
        <dt>Presentational markup indicates the appearance of the text, regardless of its purpose</dt>
        <dd>For example, <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">b</span><span class="p">&gt;</span>boldface<span class="p">&lt;/</span><span class="nt">b</span><span class="p">&gt;</span></code> indicates
          that visual output devices should render "boldface" in bold text, but gives little indication what devices that are unable to do this (such as aural devices that read the text aloud) should do. In the case of both <code class="mw-highlight"
            id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">b</span><span class="p">&gt;</span>bold<span class="p">&lt;/</span><span class="nt">b</span><span class="p">&gt;</span></code> and <code class="mw-highlight" id="" style=""
            dir="ltr"><span class="p">&lt;</span><span class="nt">i</span><span class="p">&gt;</span>italic<span class="p">&lt;/</span><span class="nt">i</span><span class="p">&gt;</span></code>, there are other elements that may have equivalent visual
          renderings but that are more semantic in nature, such as <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">strong</span><span class="p">&gt;</span>strong text<span class="p">&lt;/</span><span class="nt">strong</span><span class="p">&gt;</span></code>          and <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">em</span><span class="p">&gt;</span>emphasized text<span class="p">&lt;/</span><span class="nt">em</span><span class="p">&gt;</span></code> respectively.
          It is easier to see how an aural user agent should interpret the latter two elements. However, they are not equivalent to their presentational counterparts: it would be undesirable for a screen-reader to emphasize the name of a book, for instance,
          but on a screen such a name would be italicized. Most presentational markup elements have become <a href="https://en.wikipedia.org/wiki/Deprecation" title="Deprecation">deprecated</a> under the HTML 4.0 specification in favor of using <a href="https://en.wikipedia.org/wiki/Cascading_Style_Sheets"
            title="Cascading Style Sheets">CSS</a> for styling.</dd>
        <dt>Hypertext markup makes parts of a document into links to other documents</dt>
        <dd>An anchor element creates a <a href="https://en.wikipedia.org/wiki/Hyperlink" title="Hyperlink">hyperlink</a> in the document and its <code>href</code> attribute sets the link's target <a href="https://en.wikipedia.org/wiki/Uniform_Resource_Locator" class="mw-redirect" title="Uniform Resource Locator">URL</a>.
          For example, the HTML markup <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">a</span> <span class="na">href</span><span class="o">=</span><span class="s">"https://www.google.com/"</span><span class="p">&gt;</span>Wikipedia<span class="p">&lt;/</span><span class="nt">a</span><span class="p">&gt;</span></code>,
          will render the word "<span class="plainlinks"><a rel="nofollow" class="external text" href="https://www.google.com/">Wikipedia</a></span>" as a hyperlink. To render an image as a hyperlink, an <code>img</code> element is inserted as content
          into the <code>a</code> element. Like <code>br</code>, <code>img</code> is an empty element with attributes but no content or closing tag. <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">a</span> <span class="na">href</span><span class="o">=</span><span class="s">"https://example.org"</span><span class="p">&gt;&lt;</span><span class="nt">img</span> <span class="na">src</span><span class="o">=</span><span class="s">"image.gif"</span> <span class="na">alt</span><span class="o">=</span><span class="s">"descriptive text"</span> <span class="na">width</span><span class="o">=</span><span class="s">"50"</span> <span class="na">height</span><span class="o">=</span><span class="s">"50"</span> <span class="na">border</span><span class="o">=</span><span class="s">"0"</span><span class="p">&gt;&lt;/</span><span class="nt">a</span><span class="p">&gt;</span></code>.</dd>
      </dl>
      <h4><span class="mw-headline" id="Attributes">Attributes</span></h4>
      <div role="note" class="hatnote navigation-not-searchable">Main article: <a href="https://en.wikipedia.org/wiki/HTML_attribute" title="HTML attribute">HTML attribute</a></div>
      <p>Most of the attributes of an element are <a href="https://en.wikipedia.org/wiki/Attribute-value_pair" class="mw-redirect" title="Attribute-value pair">name-value pairs</a>, separated by <code>=</code> and written within the start tag of an element after the element's name.
        The value may be enclosed in single or double quotes, although values consisting of certain characters can be left unquoted in HTML (but not XHTML).<sup id="cite_ref-68" class="reference"><a href="#cite_note-68">[68]</a></sup><sup id="cite_ref-69"
          class="reference"><a href="#cite_note-69">[69]</a></sup> Leaving attribute values unquoted is considered unsafe.<sup id="cite_ref-70" class="reference"><a href="#cite_note-70">[70]</a></sup> In contrast with name-value pair attributes, there are
        some attributes that affect the element simply by their presence in the start tag of the element,<sup id="cite_ref-tagshtml_9-2" class="reference"><a href="#cite_note-tagshtml-9">[9]</a></sup> like the <code>ismap</code> attribute for the <code>img</code>        element.<sup id="cite_ref-71" class="reference"><a href="#cite_note-71">[71]</a></sup></p>
      <p>There are several common attributes that may appear in many elements&nbsp;:</p>
      <ul>
        <li>The <code>id</code> attribute provides a document-wide unique identifier for an element. This is used to identify the element so that stylesheets can alter its presentational properties, and scripts may alter, animate or delete its contents or
          presentation. Appended to the URL of the page, it provides a globally unique identifier for the element, typically a sub-section of the page. For example, the ID "Attributes" in <code>https://en.wikipedia.org/wiki/HTML#Attributes</code>.</li>
        <li>The <code>class</code> attribute provides a way of classifying similar elements. This can be used for <a href="https://en.wikipedia.org/wiki/Semantics" title="Semantics">semantic</a> or presentation purposes. For example, an HTML document might semantically use the designation
          <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">class</span><span class="err">="</span><span class="na">notation</span><span class="err">"</span><span class="p">&gt;</span></code> to indicate that
          all elements with this class value are subordinate to the main text of the document. In presentation, such elements might be gathered together and presented as footnotes on a page instead of appearing in the place where they occur in the HTML
          source. Class attributes are used semantically in <a href="https://en.wikipedia.org/wiki/Microformat" title="Microformat">microformats</a>. Multiple class values may be specified; for example <code class="mw-highlight" id="" style="" dir="ltr"><span class="p">&lt;</span><span class="nt">class</span><span class="err">="</span><span class="na">notation</span> <span class="na">important</span><span class="err">"</span><span class="p">&gt;</span></code>          puts the element into both the <code>notation</code> and the <code>important</code> classes.</li>
        <li>An author may use the <code>style</code> attribute to assign presentational properties to a particular element. It is considered better practice to use an element's <code>id</code> or <code>class</code> attributes to select the element from within
          a <a href="https://en.wikipedia.org/wiki/Cascading_Style_Sheets" title="Cascading Style Sheets">stylesheet</a>, though sometimes this can be too cumbersome for a simple, specific, or ad hoc styling.</li>
        <li>The <code>title</code> attribute is used to attach subtextual explanation to an element. In most <a href="https://en.wikipedia.org/wiki/Web_browser" title="Web browser">browsers</a> this attribute is displayed as a <a href="https://en.wikipedia.org/wiki/Tooltip" title="Tooltip">tooltip</a>.</li>
        <li>The <code>lang</code> attribute identifies the natural language of the element's contents, which may be different from that of the rest of the document. For example, in an English-language document:
          <div class="mw-highlight mw-content-ltr" dir="ltr">
            <pre><span class="p">&lt;</span><span class="nt">p</span><span class="p">&gt;</span>Oh well, <span class="p">&lt;</span><span class="nt">span</span> <span class="na">lang</span><span class="o">=</span><span class="s">"fr"</span><span class="p">&gt;</span>c'est la vie<span class="p">&lt;/</span><span class="nt">span</span><span class="p">&gt;</span>, as they say in France.<span class="p">&lt;/</span><span class="nt">p</span><span class="p">&gt;</span>
</pre></div>
        </li>
      </ul>
      <p>The abbreviation element, <code>abbr</code>, can be used to demonstrate some of these attributes:</p>
      <div class="mw-highlight mw-content-ltr" dir="ltr">
        <pre><span class="p">&lt;</span><span class="nt">abbr</span> <span class="na">id</span><span class="o">=</span><span class="s">"anId"</span> <span class="na">class</span><span class="o">=</span><span class="s">"jargon"</span> <span class="na">style</span><span class="o">=</span><span class="s">"color:purple;"</span> <span class="na">title</span><span class="o">=</span><span class="s">"Hypertext Markup Language"</span><span class="p">&gt;</span>HTML<span class="p">&lt;/</span><span class="nt">abbr</span><span class="p">&gt;</span>
</pre></div>
      <p>This example displays as <abbr id="anId" class="jargon" style="color:purple;" title="Hypertext Markup Language">HTML</abbr>; in most browsers, pointing the cursor at the abbreviation should display the title text "Hypertext Markup Language."</p>
      <p>Most elements take the language-related attribute <code>dir</code> to specify text direction, such as with "rtl" for right-to-left text in, for example, <a href="https://en.wikipedia.org/wiki/Arabic_language" class="mw-redirect" title="Arabic language">Arabic</a>, <a href="https://en.wikipedia.org/wiki/Persian_language"
          title="Persian language">Persian</a> or <a href="https://en.wikipedia.org/wiki/Hebrew_language" title="Hebrew language">Hebrew</a>.<sup id="cite_ref-72" class="reference"><a href="#cite_note-72">[72]</a></sup></p>
      <h3><span class="mw-headline" id="Character_and_entity_references">Character and entity references</span></h3>
      <div role="note" class="hatnote navigation-not-searchable">See also: <a href="https://en.wikipedia.org/wiki/List_of_XML_and_HTML_character_entity_references" title="List of XML and HTML character entity references">List of XML and HTML character entity references</a> and <a href="https://en.wikipedia.org/wiki/Unicode_and_HTML" title="Unicode and HTML">Unicode and HTML</a></div>
      <p>As of version 4.0, HTML defines a set of 252 <a href="https://en.wikipedia.org/wiki/Character_entity_reference" class="mw-redirect" title="Character entity reference">character entity references</a> and a set of 1,114,050 <a href="https://en.wikipedia.org/wiki/Numeric_character_reference" title="Numeric character reference">numeric character references</a>,
        both of which allow individual characters to be written via simple markup, rather than literally. A literal character and its markup counterpart are considered equivalent and are rendered identically.</p>
      <p>The ability to "escape" characters in this way allows for the characters <code>&lt;</code> and <code>&amp;</code> (when written as <code>&amp;lt;</code> and <code>&amp;amp;</code>, respectively) to be interpreted as character data, rather than markup.
        For example, a literal <code>&lt;</code> normally indicates the start of a tag, and <code>&amp;</code> normally indicates the start of a character entity reference or numeric character reference; writing it as <code>&amp;amp;</code> or <code>&amp;#x26;</code>        or <code>&amp;#38;</code> allows <code>&amp;</code> to be included in the content of an element or in the value of an attribute. The double-quote character (<code>"</code>), when not used to quote an attribute value, must also be escaped as <code>&amp;quot;</code>        or <code>&amp;#x22;</code> or <code>&amp;#34;</code> when it appears within the attribute value itself. Equivalently, the single-quote character (<code>'</code>), when not used to quote an attribute value, must also be escaped as <code>&amp;#x27;</code>        or <code>&amp;#39;</code> (or as <code>&amp;apos;</code> in HTML5 or XHTML documents<sup id="cite_ref-73" class="reference"><a href="#cite_note-73">[73]</a></sup><sup id="cite_ref-aposhtml_74-0" class="reference"><a href="#cite_note-aposhtml-74">[74]</a></sup>)
        when it appears within the attribute value itself. If document authors overlook the need to escape such characters, some browsers can be very forgiving and try to use context to guess their intent. The result is still invalid markup, which makes
        the document less accessible to other browsers and to other <a href="https://en.wikipedia.org/wiki/User_agent" title="User agent">user agents</a> that may try to parse the document for <a href="https://en.wikipedia.org/wiki/Web_crawler" title="Web crawler">search and indexing</a> purposes for
        example.</p>
      <p>Escaping also allows for characters that are not easily typed, or that are not available in the document's <a href="https://en.wikipedia.org/wiki/Character_encoding" title="Character encoding">character encoding</a>, to be represented within element and attribute content.
        For example, the acute-accented <code>e</code> (<code>é</code>), a character typically found only on Western European and South American keyboards, can be written in any HTML document as the entity reference <code>&amp;eacute;</code> or as the
        numeric references <code>&amp;#xE9;</code> or <code>&amp;#233;</code>, using characters that are available on all keyboards and are supported in all character encodings. <a href="https://en.wikipedia.org/wiki/Unicode" title="Unicode">Unicode</a> character encodings such
        as <a href="https://en.wikipedia.org/wiki/UTF-8" title="UTF-8">UTF-8</a> are compatible with all modern browsers and allow direct access to almost all the characters of the world's writing systems.<sup id="cite_ref-75" class="reference"><a href="#cite_note-75">[75]</a></sup></p>
    </div>
  </article>
</div>
</template>

